<template>
    <div>
        <yandex-map
            :scroll-zoom="scrollZoom"
            :view-all-markers="viewAllMarkers"
            :coords="coords"
            :controls="['rulerControl','zoomControl','geolocationControl']"
            :zoom="zoom"
            :style="{height: height+'px'}"
            :placemarks="placemarks"
            :use-object-manager="true"
            map-type="map"
            @map-was-initialized="initHandler"
        >
            <ymap-marker v-for="marker in markers" :key="marker.id"
                         :marker-id="marker.id"
                         :marker-type="marker.type"
                         :coords="marker.coords"
                         :hint-content="marker.hint"
                         :balloon="marker.balloon"
                         :icon="typeof(marker.icon) === 'object' ? marker.icon : map.placemark.icon"
                         :balloonTemplate="balloonTemplate"
                         :options="{
                            balloonShadow: map.placemark.balloonShadow,
                            closeButton: map.placemark.closeButton,
                            hideIconOnBalloonOpen: map.placemark.hideIconOnBalloonOpen,
                        }"
                        :properties="{

                        }"
            ></ymap-marker>
        </yandex-map>
    </div>
</template>

<script>
    export default {
        name: "Map",
        props: {
            coords: {
                default: function () {
                    return [55.633870903611, 37.439710535057]
                }
            },
            viewAllMarkers: {
                type: Boolean,
                default: false
            },
            scrollZoom: {
                type: Boolean,
                default: true
            },
            behaviors: {
                type: Array,
                default: () => ['default']
            },
            height: {default: 502},
            zoom: {
                default: function () {
                    return 9;
                }
            },
            placemarks: {
                default: function () {
                    return [/*{
                        coords: [54.8, 39.8],
                        properties: {}, // define properties here
                        options: {}, // define options here
                        clusterName: '1',
                        balloonTemplate: '<div>"Your custom template"</div>',
                        callbacks: { click: function() {} }
                    }*/]
                }
            },
            markers: {
                default: function () {
                    return [/*{
                        id: '1',
                        type: 'placemark', // define properties here
                        coords: [54.7, 39.7], // define options here
                        hint: "Hint content 1",
                        balloon: {header: 'header', body: 'body', footer: 'footer'},
                        cluster: '1',
                        coupon: {
                            image: 'https://zemskidki.ru/upload/skidki/provider/52f/52f084630df8bb35a5ea7b7fa677b66e.jpg',
                            name: 'Отделка лоджий и балконов',
                            id: 69
                        }
                    }*/]
                }
            }
        },
        data: function () {
            return {
                map: {
                    init: false,
                    controls: {
                        searchControl: false
                    },
                    placemark: {
                        icon: {
                            layout: "default#image",
                            imageHref: require('@/assets/img/baloon-stat.png'),
                            imageSize: [30, 30],
                            imageOffset: [-12, 5],
                        },
                        // Балун не имеет тени
                        balloonShadow: false,
                        closeButton: true,
                        hideIconOnBalloonOpen: false
                    }
                }
            }
        },
        computed: {
            showMap() {
                return this.$store.state.actions.showMap;
            },
            balloonTemplate() {
                return "";
            }
        },
        methods: {
            toggleMap() {
                this.$store.commit('actions/toggleMap');
            },
            initHandler() {
                this.map.init = true
            }
        }
    }
</script>
