<template>
    <div>
        <div class="content">
            <div class="main-title">
                <div class="main-title__wrap main-title--nowrap">
                    <div class="h1 text-left">Контактная информация</div>
                </div>

                <div class="main-title__right">
                    <router-link class="content-title__link --icon" :to="'/cooperation/'">
                        <span class="i i-hands2"></span>
                        <span class="text">Предложить сотрудничество</span>
                    </router-link>

                    <a href="javascript:void(0);" @click="openMailZems" class="content-title__link --icon">
                        <span class="i i-message2"></span>
                        <span class="text">Написать руководителю</span>
                    </a>
                </div>
            </div>


            <div class="contacts">
                <div class="contacts__wrap" v-html="content.description">
                </div>
                <!-- end contacts__wrap -->

                <div class="contacts__map">
                    <Map :markers="markers"
                         :height="652"
                         :coords="[55.633870903611,37.439710535057]"
                    ></Map>
                </div>
                <!-- end contacts__map -->
            </div>
            <!-- end contacts -->
        </div>


        <popup :is-set-styled="true" :show="popups.mailZems.show" :name="popups.mailZems.name">
            <mail-zems></mail-zems>
        </popup>

        <popup :is-btn-close="false" :show="popups.mailZemsDelivered.show" :name="popups.mailZemsDelivered.name">
            <mail-zems-delivered></mail-zems-delivered>
        </popup>
    </div>
</template>


<script>
    import Map from '@/components/Map.vue'
    import Popup from '@/components/modal/Popup.vue'
    import MailZems from '@/components/modal/MailZems.vue'
    import MailZemsDelivered from '@/components/modal/MailZemsDelivered.vue'
    import {mapState} from 'vuex'

    export default {
        metaInfo() {
            return {
                title: 'Контактная информация',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        mounted: function(){
            ym(39511350, 'hit', this.$route.path, {title: 'Контактная информация | ZemSkidki.ru'});
        },
        name: "Contacts",
        components: {
            Popup,
            MailZems,
            MailZemsDelivered,
            Map
        },
        created() {
            this.$store.dispatch('content/getPageContent', {pageContentID: 4020});
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                currentCity: state => state.cities.current,
            }),
            content() {
                return this.$store.getters['content/getPageContent'](4020);
            },
            markers() {
                let markers = [];

                markers.push({
                    id: 1,
                    type: 'placemark', // define properties here
                    coords: [55.427862, 37.550162], // define options here
                    hint: "",
                    balloon: {header: '', body: 'г. Подольск, ул. Комсомольская, д. 1', footer: ''},
                    cluster: '1',
                });

                markers.push({
                    id: 2,
                    type: 'placemark', // define properties here
                    coords: [55.633870903611, 37.439710535057], // define options here
                    hint: "",
                    balloon: {header: '', body: 'г. Москва, Киевское шоссе, д. 1', footer: ''},
                    cluster: '1',
                });

                markers.push({
                    id: 3,
                    type: 'placemark', // define properties here
                    coords: [55.883255349176, 37.42602464418], // define options here
                    hint: "",
                    balloon: {header: '', body: 'г. Москва, Куркинское шоссе, стр. 2', footer: ''},
                    cluster: '1',
                });

                return markers;
            },
        },
        methods: {
            openMailZems: function () {
                this.$store.dispatch('popups/open', {name: this.popups.mailZems.name});
            },

            openMailZemsDelivered: function () {
                this.$store.dispatch('popups/open', {name: this.popups.mailZemsDelivered.name});
            }
        }
    }
</script>
