<template>
    <div class="popup-info">
        <div>
            <img src="@/assets/img/icon-ok-big.svg" alt="">
        </div>

        <div class="text mt32">
            <div class="text-size-xxl text-color-green text-bolder">Ваше письмо доставлено!</div>

            <p class="mt24">Мы прочитаем его в любом случае и свяжемся с вами, если потребуется...</p>
        </div>

        <div class="mt32">
            <button class="btn btn--white-green" @click="closeMailZemsDelivered()">Степенно жду!</button>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import Popup from '@/components/modal/Popup.vue'

    export default {
        name: "mailZemsDelivered",
        components: {
            Popup
        },
        methods: {
            closeMailZemsDelivered: function () {
                this.$store.commit('popups/close', {name: this.popups.mailZemsDelivered.name});
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

